/* ✅ 전체 레이아웃 */
.post-feed {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

/* ✅ 게시물 컨테이너 */
.posts-container {
  display: flex;
  flex-direction: column;
  width: 600px;
  gap: 20px;
}

/* ✅ 게시물 헤더 */
.post-header {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: bold;
}

/* ✅ 게시물 본문 */
.post-container {
  background: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  width: 100%;
  border: 1px solid #ddd; /* 🔥 테두리 추가 */
  margin-bottom: 20px; /* 🔥 게시물 간 간격 */
}

/* ✅ 이미지 슬라이더 */
.image-slider {
  position: relative;
  width: 100%;
  height: 500px; /* ✅ 크기 고정 */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 5px;
}

/* ✅ 이미지 (고정 크기) */
.post-image {
  width: 500px;  /* 가로 크기 고정 */
  height: 500px; /* 세로 크기 고정 */
  object-fit: contain; /* ✅ 이미지가 잘리지 않고 전체 표시 */
  border-radius: 5px;
}

/* 🔥 슬라이더 컨트롤 (화살표) */
.slider-controls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
  width: 100%;
  pointer-events: none;
}

.prev-button, .next-button {
  display: flex !important;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 50%;
  pointer-events: auto;
  transition: background-color 0.2s;
}

.prev-button:hover, .next-button:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.prev-button {
  left: 5px;
}

.next-button {
  right: 5px;
}

/* ✅ 게시물 푸터 (좋아요 버튼 포함) */
.post-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 3px;
}

/* ✅ 좋아요 버튼 (하트) */
.post-like-button {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 0;
  width: auto;
}

.post-like-button:hover {
  opacity: 0.7;
}

/* ✅ 좋아요 개수 */
.post-likes {
  font-size: 16px;
  color: #333;
}

/* ✅ 태그 */
.post-tags {
  margin-top: 10px;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.post-tag {
  font-size: 14px;
  color: #555;
}

.user-posts-container {
  max-width: 600px; /* 메인 화면과 같은 너비 */
  margin: 0 auto;
  padding: 10px;
}