/* ✅ 전체 레이아웃 */
.post-feed {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

/* ✅ 왼쪽 사이드바 */
.sidebar {
  display: flex;
  flex-direction: column;
  gap: 10px;
  position: fixed;
  left: 20px;
  top: 100px;
}

.nav-link, .search-button {
  background: none;
  border: none;
  text-decoration: none;
  font-size: 16px;
  color: black;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s, color 0.2s;
}

.nav-link:hover, .search-button:hover {
  background: #f0f0f0;
  color: #007bff;
}

/* ✅ 게시물 컨테이너 */
.posts-container {
  display: flex;
  flex-direction: column;
  width: 600px;
  gap: 20px;
}

/* ✅ 게시물 본문 */
.post-container {
  background: white;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  width: 100%;
  border: 1px solid #ddd; /* 🔥 테두리 추가 */
  margin-bottom: 20px; /* 🔥 게시물 간 간격 */
}

/* ✅ 게시물 헤더 (이름과 날짜) */
.post-header {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 20px; /* 🔥 아래 간격 추가 */
}

/* ✅ 게시물 본문 */
.post-content {
  margin: 10px 0;
  font-size: 16px;
}

/* ✅ 이미지 슬라이더 (고정 크기) */
.image-slider {
  position: relative;
  width: 500px;  /* 가로 크기 고정 */
  height: 500px; /* 세로 크기 고정 */
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  border-radius: 5px;
  background-color: #f8f8f8; /* 빈 공간이 있을 때 배경색 추가 */
}

/* ✅ 이미지 (잘리지 않으면서 컨테이너 안에 맞춤) */
.post-image {
  width: 100%;
  height: 100%;
  object-fit: contain; /* ✅ 이미지 전체를 표시하면서 빈 공간은 유지 */
  border-radius: 5px;
}

/* 🔥 슬라이더 컨트롤 (화살표) */
.slider-controls {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  transform: translateY(-50%);
  width: 100%;
  pointer-events: none;
}

.prev-button, .next-button {
  display: flex !important;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.3);
  color: white;
  border: none;
  padding: 5px;
  cursor: pointer;
  font-size: 16px;
  border-radius: 50%;
  pointer-events: auto;
  transition: background-color 0.2s;
}

.prev-button:hover, .next-button:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.prev-button {
  left: 5px;
}

.next-button {
  right: 5px;
}

/* ✅ 게시물 푸터 (좋아요 버튼 포함) */
.post-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 3px;
}

/* ✅ 좋아요 버튼 (하트) */
.post-like-button {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 0;
  width: auto;
}

.post-like-button:hover {
  opacity: 0.7;
}

/* ✅ 좋아요 개수 */
.post-likes {
  font-size: 16px;
  color: #333;
}

/* ✅ 태그 */
.post-tags {
  margin-top: 10px;
}

.tags {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.post-tag {
  font-size: 14px;
  color: #555;
}