.profile-header {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px;
  margin-bottom: 20px;
}

.profile-info {
  text-align: left;
}

.nickname {
  font-size: 20px;
  font-weight: bold;
}

.follow-state {
  font-size: 14px;
  color: gray;
}

.profile-container {
  max-width: 600px;
  margin: 0 auto;
  text-align: center;
}

.profile-picture {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.post-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 5px;
  margin-top: 20px;
}

.post-item {
  width: 100%;
  padding-top: 100%;
  position: relative;
  background-color: #f0f0f0;
  cursor: pointer;
}

.post-thumbnail {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

